<template lang="jade">
section.section(v-show="githubs")
  h2.section-header Github Activities
  .list-item(v-for="activity in githubs")
    p.list-description
      a(v-bind:href="activity.url", target="_blank") {{activity.description}}
</template>

<script>
export default {
  props: ['githubs']
}
</script>

<style lang="scss" scoped>
$list-header-color: #323336;

.list-description {
  a {
    color: $list-header-color;
    text-decoration: none;
  }

  a:hover {
   text-decoration: underline;
  }
}
</style>



