<template lang="jade">
section.section(v-show="projects")
  h2.section-header Projects
  .list-item(v-for="project in projects")
    h3.list-header(v-show="!project.url")
      | {{project.title}}
    h3.list-header.has-link(v-show="project.url")
      a(v-bind:href="project.url", target="_blank") {{project.title}}
    p.list-meta {{project.meta}}
    p.list-description {{project.description}}
    .row
      .four.columns(v-for="image in project.images")
        a.item(v-bind:href="image.original")
          img.u-full-width(v-bind:src="image.thumbnail")
</template>

<script>
export default {
  props: ['projects']
}
</script>

<style lang="scss" scoped>
$list-header-color: #323336;

.list-header.has-link a {
  color: $list-header-color;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}
</style>



